import React from 'react';
import './App.css';
import {
	HashRouter as Router,
	Switch,
	Link,
	Route
} from 'react-router-dom'
import SimpleCard from './components/ExampleCards/SimpleCard/SimpleCard';
import SneakerCard from './components/ExampleCards/SneakerCard/SneakerCard';
import ArcSlider from './components/ArcSlider/ArcSlider';
import { useState } from 'react/cjs/react.development';

function App() {

	const [slider1, setSlider1] = useState(0);

	return (
		<Router>
			<div className="App">
				<ul className="nav">
					<li>
						<Link className="link" to="/">3D Cards</Link>
					</li>
					<li>
						<Link className="link" to="/slider">Sliders</Link>
					</li>
				</ul>
				<Switch>
					<Route exact path="/">
						<div className="container">
							<SimpleCard>
								<span>Some Text</span>
								<input placeholder="Some Input..." />
								<button>Some button</button>
								<span>Another Text</span>
							</SimpleCard>

							<SimpleCard config={{ mode: 'bottomToTop', offsetStep: 20 }}>
								<span>Bottom To Top</span>
								<input placeholder="Some Input..." />
								<button>Some button</button>
								<span>Simple card</span>
							</SimpleCard>

							<SimpleCard className="card-with-backgroung">
								<span>Card with backgroung</span>
								<span>Simple card</span>
							</SimpleCard>
							<SneakerCard></SneakerCard>
						</div>
					</Route>
					<Route path="/slider">
						<div className="container">
							<div className="vc">
								<div className='vc'>
									<span style={{ fontSize: '45px' }}>{slider1}</span>
									<ArcSlider onChange={(value) => { setSlider1(value) }} />
								</div>
							</div>


						</div>

					</Route>
				</Switch>
			</div>
		</Router>
	);
}

export default App;
