import React, { useState } from "react"
import './ArcSlider.css'

let debug = []
let arc = {}
function ArcSlider(props) {

	let progressArc = {}

	let [mouse, setMouse] = useState({ x: 0, y: 0 })
	let [isMouseDown, setMouseDown] = useState(false)

	const padding = props.padding || 5;
	const strokeColor = props.strokeColor || "grey"
	const strokeWidth = props.strokeWidth || 5;
	const progressColor = props.progressColor || "blue"
	const progressWidth = props.progressWidth || 10;
	const base = props.base || 300;
	const vertical = props.vertical || 120;
	const knobColor = props.knobColor || "green";
	const knobRadius = props.knobRadius || 30;
	const minValue = props.minValue || 0;
	const maxValue = props.maxValue || 100;

	const onChange = props.onChange ? props.onChange : (value) => { }


	arc = calculateArc(base / 2, vertical, base, vertical)

	let width = arc.dir === 1 ? 2 * arc.radius : base;
	width += padding * 2 + 2 * knobRadius
	const height = vertical + knobRadius * 2 + padding * 2;

	const offsetX = arc.dir === 1 ? (arc.radius * 2 - base + 2 * knobRadius + padding) / 2 : knobRadius
	const offsetY = knobRadius + padding;

	let [knobCoords, setKnobCoords] = useState({ x: arc.x1 + offsetX, y: arc.y1 + offsetY })


	function mouseDownHandler(e) {
		setMouseDown(true)
	}
	function mouseUpHandler(e) {
		setMouseDown(false)
	}
	function mouseMoveHandler(e) {
		e.preventDefault();
		const rect = e.currentTarget.getBoundingClientRect();
		const [mouseX, mouseY] = [e.clientX - rect.left, e.clientY - rect.top]

		setMouse({ x: mouseX, y: mouseY })
		if (isMouseDown) {
			setKnobCoords(calculateKnobCoords(arc.cx + offsetX, arc.cy + offsetY, arc.x1 + offsetX, arc.y1 + offsetY, arc.bx + offsetX, arc.by + offsetY, arc.radius, mouseX, mouseY))
		}

	}

	function calculateKnobCoords(cx, cy, x1, y1, bx, by, r, mouseX, mouseY) {

		let [kx, ky] = toUnitVector(cx, cy, mouseX, mouseY)

		kx *= r;
		ky *= r;
		kx += cx;
		ky += cy;
		let sign = cy < ky ? -1 : 1;
		let x = kx;
		let ySqr = r * r - (x - cx) * (x - cx)
		let y = sign * Math.sqrt(ySqr) - cy
		y = -y

		if (y > by) {
			if (x < bx) {
				x = x1;
				y = y1;
			}
			if (x > bx) {
				x = arc.x2 + offsetX;
				y = arc.y2 + offsetY;
			}
		}
		let angle = angleBetween(bx, by, x1, y1, x, y);

		let mapped = Math.trunc(mapRange(angle, 0, 180, minValue, maxValue))

		onChange(mapped)

		calculateProgressArc(x1, y1, x, y)

		return {
			x: x,
			y: y
		}
	}

	return (
		<div
			className='arc-slider'
			onMouseDown={mouseDownHandler}
			onMouseMove={mouseMoveHandler}
			onMouseUp={mouseUpHandler}
		>
			<div className='arc-slider__wrapper'>
				<svg
					width={`${width}px`}
					height={`${height}px`}
					viewBox={`0 0 ${width} ${height}`}
					style={{
						padding: `${padding}px`,
						boxSizing: "border-box",
					}}
				>
					{debug}
					<line x1={arc.cx + offsetX} y1={arc.cy + offsetY} x2={mouse.x} y2={mouse.y} />
					<path
						style={{
							strokeLinecap: "round",
							strokeWidth: `${strokeWidth}px`,
							stroke: `${strokeColor}`,
							fill: "none",
						}}
						d={pathGenerator(offsetX + arc.x1, arc.y1 + offsetY, arc.radius, arc.dir, offsetX + arc.x2, arc.y2 + offsetY)}
					></path>

					<path
						style={{
							strokeLinecap: "round",
							strokeWidth: `${progressWidth}px`,
							stroke: `${progressColor}`,
							fill: "none",
						}}
						d={pathGenerator(offsetX + arc.x1, arc.y1 + offsetY, arc.radius, arc.dir, knobCoords.x, knobCoords.y)}
					></path>

					<circle
						r={knobRadius}
						cx={knobCoords.x}
						cy={knobCoords.y}
						style={{
							fill: knobColor,
							cursor: "pointer",
						}}
					/>
				</svg>
			</div>

		</div>
	)
};


function calculateProgressArc(x1, y1, x2, y2) {

	let angle1 = angleBetween(arc.cx, arc.cy, x1, y1, x2, y2)
	let angle2 = angleBetween(x1, y1, arc.cx, arc.cy, x2, y2)


	return {

	}
}

function calculateArc(cx, cy, base, vertical) {
	const a = base / 2;
	const b = vertical;
	const [x1, y1] = [cx - a, cy]
	const [x2, y2] = [cx + a, cy]
	const [x3, y3] = [cx, cy - b]
	const dir = a > b ? 0 : 1;

	const [mx, my] = midPoint(x1, y1, x3, y3)

	let [vx, vy] = toUnitVector(x1, y1, mx, my)

	let t = vx;
	vx = -vy * 5000
	vy = t * 5000;

	vx = mx + vx;
	vy = my + vy;

	const intersecton = checkLineIntersection(cx, cy + 10000, x3, y3, mx, my, vx, vy)
	const [rx, ry] = intersecton ? intersecton : [x3, y3]
	const radius = distanceBetwen(x3, y3, rx, ry)


	/*drawLine(x1, y1, x3, y3, 'black')
	drawLine(cx, cy, x3, y3, 'black')
	drawLine(mx, my, vx, vy, 'red')*/

	return {
		x1,
		y1,
		x2,
		y2,
		radius,
		dir,
		bx: cx,
		by: cy,
		cx: rx,
		cy: ry,
		base,
		vertical
	}

	//pathGenerator(x1, y1, radius, dir, x2, y2);
}

function drawLine(x1, y1, x2, y2, color = "black", force = false) {
	if (force) {
		debug[0] = <line x1={x1} y1={y1} x2={x2} y2={y2} stroke={color} key={Math.random()} />
	} else {
		debug.push(<line x1={x1} y1={y1} x2={x2} y2={y2} stroke={color} key={Math.random()} />)
	}
}

function angleBetween(cx, cy, x1, y1, x2, y2) {

	let [ax, ay] = toUnitVector(cx, cy, x1, y1)
	let [bx, by] = toUnitVector(cx, cy, x2, y2)

	let dp = dotProduct(ax, ay, bx, by)

	let angle = Math.acos(dp)

	return (angle * (180 / Math.PI))
}

function magnitude(x, y) {
	return Math.sqrt(x * x + y * y)
}

function dotProduct(x1, y1, x2, y2) {
	return (x1 * x2 + y1 * y2);
}

function pathGenerator(x1, y1, radius, direction, x, y) {
	const points = [];
	points.push("M" + x1);
	points.push(y1);
	points.push("A");
	points.push(radius);
	points.push(radius);
	points.push(0);
	points.push(direction);
	points.push(1);
	points.push(x);
	points.push(y);
	return points.join(" ");
}

function midPoint(x1, y1, x2, y2) {
	return [
		(x1 + x2) / 2,
		(y1 + y2) / 2
	]
}

function toUnitVector(x1, y1, x2, y2) {
	let dist = distanceBetwen(x1, y1, x2, y2);
	return [(x2 - x1) / dist
		, (y2 - y1) / dist];
}

function distanceBetwen(x1, y1, x2, y2) {
	const px1 = Math.min(x1, x2);
	const py1 = Math.min(y1, y2);

	const px2 = Math.max(x1, x2);
	const py2 = Math.max(y1, y2);

	return Math.sqrt((px2 - px1) * (px2 - px1) + (py2 - py1) * (py2 - py1));

}

function checkLineIntersection(x1, y1, x2, y2, x3, y3, x4, y4) {
	let den = ((x1 - x2) * (y3 - y4) - (y1 - y2) * (x3 - x4));
	if (den === 0) return false;

	let t = ((x1 - x3) * (y3 - y4) - (y1 - y3) * (x3 - x4)) / den;
	let u = -((x1 - x2) * (y1 - y3) - (y1 - y2) * (x1 - x3)) / den;

	if (t > 0 && t < 1 && u > 0 && u < 1) {
		return [(x1 + t * (x2 - x1)), (y1 + t * (y2 - y1))];
	}

	return false;
}

function mapRange(value, low1, high1, low2, high2) {
	if (value < low1) value = low1;
	if (value > high1) value = high1;
	return low2 + (high2 - low2) * (value - low1) / (high1 - low1);
}

export default ArcSlider;
